.story-container {
    max-width: 600px;
    margin: 0 auto;
    margin-top: 10vh;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center; /* Alinea los elementos al centro horizontalmente */
  }
  
  .category-heading {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    text-align: center;
  }
  

  .button-container {
    width: 100%;
    display: flex;
    justify-content: space-between; /* Distribuye los elementos al espacio entre ellos */
  }
  
  .reload-button,
  .go-back-button {
    flex: 1; /* Hace que ambos botones ocupen la mitad del espacio disponible */
    background-color: #157cb8;
    color: #fff;
    padding: 10px 15px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 10px;
   
    transition: background-color 0.3s ease;
  }
  
  .reload-button:hover {
    background-color: #189fed;
  }
  
  .go-back-button {
    background-color: #d9534f;
    margin-left: 10px; /* Espaciado entre los botones */
  }
  
  .go-back-button:hover {
    background-color: #c9302c;
  }
  .section-paragraph {
    opacity: 0; /* Configura la opacidad inicial a 0 */
    margin-bottom: 10px;
    transition: opacity 0.5s ease-in-out; /* Agrega una transición de opacidad */
  }
  .section-paragraph.visible {
    opacity: 1; /* Configura la opacidad a 1 para hacer visible el párrafo */
  }