.welcome-container {
    
    max-width: 600px;
    margin: 0 auto;
    margin-top: 10vh;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center; /* Centra los elementos horizontalmente */
  }
  
  .title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .link-button {
    display: block;
    width: 70%; /* Todos los botones tienen el mismo ancho */
    margin: 10px 0; /* Espaciado entre los botones */
    text-decoration: none;
    color: #fff;
    background-color: #007bff;
    border: 1px solid #007bff;
    border-radius: 4px;
    padding: 10px 15px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .link-button:hover {
    background-color: #0056b3;
  }
  